.home-intro {
  padding-top:10rem; padding-bottom:10rem
}

@media(min-width:768px) {
  .home-intro {
    padding-bottom:8rem
  }
}

@media(min-width:1024px) {
  .home-intro {
    padding-top:15rem; padding-bottom:12.6rem
  }
}

@media(min-width:1240px) {
  .home-intro {
    padding-top:11.90476vw; padding-bottom:11.90476vw
  }
}

.home-intro__wrapper {
  grid-column:span 24
}

@media(min-width:768px) {
  .home-intro__wrapper {
    grid-column:11/span 24
  }
}

.blurb {
  position:relative
}

.blurb__content {
  text-indent:2.4rem
}

@media(min-width:1024px) {
  .blurb__content {
    text-indent:3.8rem
  }
}

@media(min-width:1240px) {
  .blurb__content {
    text-indent:4.8rem
  }
}

.typewriter {
  padding: 0 4px 0 0;
  border-right: 2px solid #747474;
  animation: blink 0.75s infinite;
  width: 100px;
}

.red {
  color: #ff0000;
}

@keyframes blink {
  from {
    border: none;
  }
  to {
    border-color: 2px solid #747474;
  }
}