.home-hero {
  position: relative;
  color: #eaeef4;
  padding-top: 5.3rem;
  padding-bottom: 1.6rem;
  min-height: 100svh;
  display: flex;
  background-color: #1e1e1e;
  overflow: hidden;
}

@media(min-width: 1024px) {
  .home-hero {
    padding-top: 6rem;
    padding-bottom: 2.4rem;
  }
}

.home-hero__wrapper {
  position:relative;
  z-index:1;
  flex:1;
  pointer-events:none;
  padding-top: 1.5rem;
}

.home-hero__logo-wrapper {
  grid-column-end:span 24; margin-bottom:auto
}

@media(min-width:1024px) {
  .home-hero__logo-wrapper {
    grid-column-end:span 24
  }
}

.home-hero__logo {
  fill:#eaeef4
}

.home-hero--intro .home-hero__logo {
  overflow:hidden
}

.home-hero--intro .home-hero__logo .logo__letter {
  transform:translateY(100%); transition:transform .5s cubic-bezier(.9, 0, .2, 1)
}

.home-hero--intro .home-hero__logo .logo__letter:first-child {
  transition-delay:.05s
}

.home-hero--intro .home-hero__logo .logo__letter:nth-child(2) {
  transition-delay:.1s
}

.home-hero--intro .home-hero__logo .logo__letter:nth-child(3) {
  transition-delay:.15s
}

.home-hero--intro .home-hero__logo .logo__letter:nth-child(4) {
  transition-delay:.2s
}

.home-hero--intro .home-hero__logo .logo__letter:nth-child(5) {
  transition-delay:.25s
}

.home-hero--intro .home-hero__logo .logo__letter:nth-child(6) {
  transition-delay:.3s
}

.home-hero--intro .home-hero__logo--visible .logo__letter {
  transform:translateY(0)
}

.home-hero__inner {
  margin-top:auto; text-align:center; grid-column:1/span 24
}

@media(min-width:768px) {
  .home-hero__inner {
    position:relative; grid-column:6/span 14
  }
}

@media(max-width:767px) {
  .home-hero__title {
    font-family:GroteskMonoWeb, sans-serif; font-size:1.2rem; line-height:1.3; letter-spacing:-.02em
  }
}

@media(min-width:768px) {
  .home-hero__title {
    margin-bottom:2.4rem; position:absolute; bottom:100%; left:50%; transform:translateX(-50%); white-space:nowrap
  }
}

.home-hero__list {
  display:none
}

@media(min-width:768px) {
  .home-hero__list {
    display:flex; flex-wrap:wrap; justify-content:center; grid-row-gap:.8rem; row-gap:.8rem; grid-column-gap:.8rem; -moz-column-gap:.8rem; column-gap:.8rem; max-width:72rem; margin:0 auto
  }
}

.home-hero__list-item {
  pointer-events:none
}

.home-hero__asset {
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.home-hero__asset, .home-hero__asset-trigger {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.base-video {
  position: relative;
  display: flex;
}

.base-video--fit {
  width: 100%;
  height: 100%;
}

video {
  object-fit: contain;
  overflow-clip-margin: content-box;
  overflow: clip;
}

.base-video--fit video {
  position: absolute;
  top: 0;
  left: 0;
  -o-object-fit: cover;
  object-fit: cover;
}

.base-video video {
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity .4s;
}

.base-video--loaded video {
  opacity: 1;
}

.base-video {
  position: relative;
  display: flex;
}

.home-hero__overlay {
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background-color:transparent;
  pointer-events:none
}

.home-hero__overlay--darken {
  background-color:#000;
  opacity: 0.35;
}