.cta-1 {
  font-size: 1.3rem;
}

.cta-1, .cta-2 {
  line-height: 1;
  letter-spacing: .02em;
}

.ttu {
  text-transform: uppercase;
}

.btn {
  cursor: pointer;
  border-radius: 5.6rem;
  //border-radius: 0rem;
  color: currentColor;
}

//@media inhover {
//  .btn {
//    transition: background-color .6s cubic-bezier(.75,.1,.25,1),color .6s cubic-bezier(.75,.1,.25,1);
//  }
//}

.btn--primary {
  background-color: #000;
  color: #eaeef4;
  padding: .8rem 1.6rem .7rem;
}

.btn--secondary {
  border:.1rem solid #000; padding:.8rem 1.6rem .7rem
}

@media(min-width:1024px) {
  .btn--secondary {
    padding:1.2rem 1.6rem 1.1rem
  }
}

.btn--transparent {
  color:#eaeef4; border-color:rgba(234, 238, 244, .15)
}

.btn--blur {
  -webkit-backdrop-filter: blur(1rem);
  backdrop-filter: blur(1rem);
  background-color: rgba(0,0,0,.4);
}

.btn--small {
  padding:.4rem 1.6rem
}

@media(min-width:1024px) {
  .btn--primary {
    padding: 1.2rem 1.6rem 1.1rem;
  }
}
@media(hover: hover) {
  .btn {
    transition:background-color .6s cubic-bezier(.75, .1, .25, 1), color .6s cubic-bezier(.75, .1, .25, 1)
  }

  .btn:hover {
    transition:none
  }

  .btn:hover.btn--primary {
    background-color:rgba(0, 0, 0, .4)
  }

  .btn:hover.btn--blur, .btn:hover.btn--secondary {
    background-color:#000; color:#eaeef4
  }

  .btn:hover.btn--transparent {
    background-color:rgba(234, 238, 244, .15)
  }
}

a:active, a:hover, input, select, textarea {
  outline: 0;
}

@media(max-width:767px) {
  .nav-item .btn {
    -webkit-backdrop-filter:none; backdrop-filter:none; background-color:transparent; color:currentColor; padding:unset; border-radius:0
  }

  .nav-item .btn, .nav-item .btn-label {
    font-family:GroteskMonoWeb, sans-serif
  }

  .nav-item .btn-label {
    text-transform:unset; text-decoration:underline; font-size:1.5rem; line-height:1.3; letter-spacing:-.02em
  }
}

@media(max-width:767px)and (min-width:768px) {
  .nav-item .btn-label {
    font-size:1.2rem
  }
}