.project-item__info {
  color: rgba(0, 0, 0, 0.4);
}

.project-item__content {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 0.8rem;
  border-right: 0.1rem solid rgba(0, 0, 0, 0.15);
}

@media (min-width: 1240px) {
  .project-item__content {
    padding: 0.71429vw;
    flex-direction: row;
  }
}
.project-item--project .project-item__content {
  flex: 0 0 14.8rem;
}

@media (min-width: 1240px) {
  .project-item--project .project-item__content {
    flex: 0 0 14.88095vw;
  }
}
@media (min-width: 1240px) {
  .project-item--collection .project-item__inner:first-child {
    flex: 0 0 9.64286vw;
    padding-right: 1.42857vw;
  }
}
@media (min-width: 1240px) {
  .project-item--project .project-item__inner:first-child {
    flex: 0 0 12.20238vw;
    padding-right: 2.38095vw;
  }
  .project-item:nth-child(n+5):nth-child(-n+8) .project-item--project .project-item__inner:first-child {
    flex: 0 0 24.40476vw;
    padding-right: unset;
  }
}
.project-item__inner:nth-child(2) {
  display: flex;
  justify-content: space-between;
  margin-top: auto;
}

@media (min-width: 1240px) {
  .project-item__inner:nth-child(2) {
    flex: 1;
    margin-top: unset;
  }
}
.projec-item__title {
  display: flex;
  align-items: center;
  margin-bottom: 0.3rem;
}

.projec-item__svg {
  display: none;
}

.project-item--collection .projec-item__svg {
  display: flex;
  transform: rotate(-180deg);
  width: 1rem;
  height: 1rem;
  margin-right: 0.4rem;
}

@media (min-width: 1240px) {
  .project-item--collection .projec-item__svg {
    width: 0.65476vw;
    margin-right: 0.2381vw;
    height: 0.65476vw;
  }
}
.project-item__link {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.base-image {
  display: block;
}

.base-image--fit {
  height: 100%;
}

.base-image--fit img {
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}

.base-image__placeholder {
  opacity: 0;
}

.base-image__img--fade {
  opacity: 0;
  transition: opacity 0.4s;
}

.base-image__img--loaded {
  opacity: 1;
}

