.project-list__grid {
  display:flex;
  flex-wrap:wrap
}

.section-title {
  padding-bottom:.8rem
}

@media(min-width:1024px) {
  .section-title {
    padding-bottom:1.2rem
  }
}

@media(min-width:1240px) {
  .section-title {
    padding-bottom:.71429vw
  }
}

.section-title__content {
  display:flex; align-items:baseline; grid-column:1/span 24; font-family:GroteskNeueWeb, sans-serif
}

@media(min-width:768px) {
  .section-title__content {
    grid-column:13/span 12
  }
}

.section-title__svg {
  width:1rem; height:.8rem; margin-right:.4rem
}

.project-item--project {
  flex:0 0 50%
}

@media(max-width: 1023px){
  .project-item--project:nth-child(1), .project-item--project:nth-child(2) {
    flex:0 0 100%
  }
}

@media(min-width:1024px)and (max-width:1239px) {
  .project-item--project:nth-child(n+3):nth-child(-n+8) {
    flex:0 0 33.3333%
  }
}

@media(min-width:1240px) {
  .project-item--project:nth-child(n+5):nth-child(-n+8) {
    flex:0 0 25%
  }
}

.project-item__fig-wrapper {
  position:relative
}

.project-item--project .project-item__fig-wrapper {
  flex:0 0 22rem
}

@media(min-width:768px) {
  .project-item--project .project-item__fig-wrapper {
    flex:0 0 43.2rem
  }
}

@media(min-width:1024px) {
  .project-item--project .project-item__fig-wrapper {
    flex:0 0 28.8rem
  }
}

@media(min-width:1240px) {
  .project-item--project .project-item__fig-wrapper {
    flex:0 0 28.15476vw
  }
}

.project-item--project .project-item__fig {
  position:absolute; top:0; left:0; width:100%; height:100%
}

.project-item__info {
  color:rgba(0, 0, 0, .4)
}

.project-item__content {
  display:flex; flex-direction:column; flex:1; padding:.8rem; border-right:.1rem solid rgba(0, 0, 0, .15)
}

@media(min-width:1240px) {
  .project-item__content {
    padding:.71429vw; flex-direction:row
  }
}

.project-item--project .project-item__content {
  flex:0 0 14.8rem
}

@media(min-width:1240px) {
  .project-item--project .project-item__content {
    flex:0 0 14.88095vw
  }
}

@media(min-width:1240px) {
  .project-item--collection .project-item__inner:first-child {
    flex:0 0 9.64286vw; padding-right:1.42857vw
  }
}

@media(min-width:1240px) {
  .project-item--project .project-item__inner:first-child {
    flex:0 0 12.20238vw; padding-right:2.38095vw
  }

  .project-item:nth-child(n+5):nth-child(-n+8) .project-item--project .project-item__inner:first-child {
    flex:0 0 24.40476vw; padding-right:unset
  }
}

.project-item__inner:nth-child(2) {
  display:flex; justify-content:space-between; margin-top:auto
}

@media(min-width:1240px) {
  .project-item__inner:nth-child(2) {
    flex:1; margin-top:unset
  }
}

.projec-item__title {
  display:flex; align-items:center; margin-bottom:.3rem
}

.projec-item__svg {
  display:none
}

.project-item--collection .projec-item__svg {
  display:flex; transform:rotate(-180deg); width:1rem; height:1rem; margin-right:.4rem
}

@media(min-width:1240px) {
  .project-item--collection .projec-item__svg {
    width:.65476vw; margin-right:.2381vw; height:.65476vw
  }
}

.project-item__link {
  display:flex; flex-direction:column; height:100%
}

.base-image {
  display: block
}

.base-image--fit {
  height: 100%
}

.base-image--fit img {
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover
}

.base-image__placeholder {
  opacity: 0
}

.base-image__img--fade {
  opacity: 0;
  transition: opacity .4s
}

.base-image__img--loaded {
  opacity: 1
}