.header {
  display: flex;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  justify-content: flex-end;

  padding: 1.2rem .8rem;
  position: fixed;
}

@media(min-width: 1024px) {
  .header {
    position:absolute;
    padding: 1.2rem
  }
}

@media(min-width: 1240px) {
  .header {
    padding:.71429vw
  }
}

.nav {
  display: block;
  box-sizing: border-box;
}

.nav__list-item {
  display: flex;
}

@media (min-width: 1240px) {
  .nav__list-item {
    margin-right: .95238vw;
  }
}

@media (min-width: 1024px) {
  .nav__list-item {
    margin-right: 1.6rem;
  }
}

@media (min-width: 768px) {
  .nav__list-item {
    margin-bottom: unset;
    margin-right: .8rem;
  }
}

.nav__list-item {
  //margin-bottom: 1.4em;
}

.nav-item {
  display: flex;
}

.nav__list {
  display: flex;
}

