.footer__grid {
  padding-top:.8rem; padding-bottom:.8rem
}

@media(min-width:1024px) {
  .footer__grid {
    height:100vh; height:100svh; padding-top:1.2rem; padding-bottom:1.2rem
  }
}

@media(min-width:1240px) {
  .footer__grid {
    padding-top:.71429vw; padding-bottom:.71429vw
  }
}

.footer__square {
  display:none
}

@media(min-width:768px) {
  .footer__square {
    display:flex; grid-column:1/span 1
  }
}

.footer__square-svg {
  width:3.3rem; height:3.3rem
}

@media(min-width:1240px) {
  .footer__square-svg {
    width:1.96429vw; height:1.96429vw
  }
}

.footer__col--left {
  grid-column:span 24
}

@media(min-width:768px) {
  .footer__col--left {
    grid-column:13/span 6
  }
}

.footer__col--right {
  grid-column:span 24
}

@media(min-width:768px) {
  .footer__col--right {
    grid-column:19/span 6
  }
}

.footer__logo {
  display:flex; grid-column:1/span 24
}

@media(min-width:1024px) {
  .footer__logo {
    grid-column-end:span 24
  }
}

.footer__logo-link {
  display:flex;
  width:100%;
  align-items:flex-end
}

.footer__logo-link svg {
  height:auto
}

.footer__plus {
  display:flex; grid-column:13/span 1; padding:10.4rem 0 8rem; transform:translateX(-.9rem)
}

@media(min-width:768px) {
  .footer__plus {
    padding:23rem 0 20.6rem
  }
}

@media(min-width:1024px) {
  .footer__plus {
    padding:unset; margin:auto 0
  }
}

@media(min-width:1240px) {
  .footer__plus {
    transform:translateX(-.53571vw)
  }
}

.footer__plus-svg {
  width:1.8rem; height:1.8rem
}

@media(min-width:1240px) {
  .footer__plus-svg {
    width:1.07143vw; height:1.07143vw
  }
}

.footer__content-left a:before {
  display:none
}

.footer-nav {
  margin:1.6rem 0
}

@media(min-width:768px) {
  .footer-nav {
    margin-bottom:unset
  }
}

.footer-nav__link {
  text-decoration:underline
}

.footer-nav-item__link {
  display:inline-flex; color:currentColor
}